.absolute_header {
    position: absolute;
    bottom : 0;
}

.ingredients__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 24px;
    grid-row-gap: 32px;
}

.ingredient_popup {
    position: fixed;
    top:0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,.6);
    transition: all ease-in-out 0.12s;
}

.popup__body {
    position: absolute;
    z-index: 2;
    border-radius: 40px;
    background-color: #1C1C21;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    min-width: 640px;
}

.close {
    background-color: transparent;
    border:none;
    outline: none;
    position: absolute;
    right: 30px;
    top: 30px;
    cursor: pointer;
}
